export async function createRoutines() {
  try {
    const { data } = await axios.post(`${BASE_URL}/routines`, {
      method: "POST",
      headers: { Authorization: "Bearer" + getCurrentToken() },
      body: JSON.stringify({
        creatorId,
        isPublic,
        name,
        goal,
        activities,
      }),
    });
    return data;
  } catch (error) {
    throw error;
  }
}

export async function deleteRoutine(id) {
  try {
    const { data } = await axios.delete(`${BASE_URL}/routines/${id}`, {
      headers: { Authorization: "Bearer " + getCurrentToken() },
    });
    return data;
  } catch (error) {
    throw error;
  }
}

